@import './variables.scss';

/* ë¡œë˜ ê¸°ëŠ¥ ê³µìœ  ìŠ¤íƒ€ì¼ */
#app {
  .lotto-title {
    display: grid;
    grid-template-columns: 80% 20%;
    margin: 1rem 0;

    > *:last-child {
      text-align: right;
    }
  }

  section.show {
    display: block;
  }
}

/* ë¡œë˜ ê¸ˆì•¡ ìž…ë ¥ View ìŠ¤íƒ€ì¼ */
section.lotto-money-section {
  .lotto-money-input-container {
    display: grid;
    grid-template-columns: 80% 15%;
    gap: 5%;
  }
}

/* ë¡œë˜ ëª©ë¡ ìž…ë ¥ View ìŠ¤íƒ€ì¼ */
section.lotto-list-section {
  display: none;

  .lotto-list-container {
    display: grid;
    grid-template-columns: 80% 15%;
    gap: 5%;

    > div:last-child {
      padding-left: 1rem;
    }
  }

  .lotto-item-container {
    max-height: 15rem;
    overflow-y: scroll;
    > .item {
      display: inline-block;

      > span {
        font-size: 2rem;
        vertical-align: middle;
      }

      > span.item-number {
        display: none;
        font-size: 1rem;
      }
    }

    &.list {
      background-color: #f3f3f3;
      border-radius: $border-radius;
      padding: 0 1rem;

      > .item {
        display: block;

        > span.item-number {
          display: inline-block;
        }
      }
    }
  }
}

/* ë¡œë˜ ë‹¹ì²¨ë²ˆí˜¸ ìž…ë ¥ View ìŠ¤íƒ€ì¼ */
section.winning-number-section {
  display: none;

  .winning-number-input-container {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    margin-bottom: 2rem;
    gap: 0.3rem;

    > .winning-number-input {
      width: 100%;
      grid-column: 1 span;
      text-align: center;
      padding: $form-item-padding 0 !important;
    }

    & .winning-number-input:last-child {
      grid-column: 9;
    }
  }
}

/* ë¡œë˜ ê²°ê³¼ ì¶œë ¥ View ìŠ¤íƒ€ì¼ */
section#lotto-result-modal {
  #lotto-result-list {
    tr:hover {
      background-color: #f3f3f3;
    }

    td.bold {
      font-weight: bold;
    }
  }

  #lotto-yield-text {
    font-weight: bold;
    text-align: center;
    margin-bottom: 2rem;
  }

  #hide-result-button {
    display: block;
    width: 50%;
    margin: 0 auto;
  }
}
